	text-decoration: none;
	color: #2A56C6;
}

.menu-item {
	position: relative;
	margin-left: 3vw;
	right: 8vw;
	margin-top: 2.5vh;
	cursor: pointer;
}

.menu-link {
	position: relative;
	font-size: 20px;
	font-weight: 300;
	color: black;
	text-decoration: none;
}

.menu-link:hover {
	color: black;
}

.menu-link:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: -3px;
	left: 0;
	background-color: black;
	visibility: hidden;
	-webkit-transform: scaleX(0);
	transform: scaleX(0);
	transition: all 0.3s ease-in-out 0s;
}

.menu-link:hover:before {
	visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

.menu-link-selected:after {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: -3px;
	left: 0;
	background-color: black;
	-webkit-transform: scaleX(1);
    transform: scaleX(1);
}
